html{
    background: #100e17;
    color: #fff;
    font-size: 1.2rem;
    font-weight: 400;
    line-height: 1.7;
    font-family: Ringside Regular A,Ringside Regular B,
                 Rubik,Lato,Lucida Grande,Lucida Sans Unicode,
                 Tahoma,Sans-Serif;
  }
  
  
  *{
      box-sizing: border-box;
  }
  
  html,body,menu,ul,ol {
    margin: 0;
    padding: 0;
  }
  
  
  
  .nav-bar a {
    text-decoration: none;
  }
  
  .nav-bar {
    display:flex;
    width: 100%;
    height: 5rem;
  }
  
  .nav-bar div {
    background: #100e17;
    box-shadow:
    1px 1px 0 0 #201c29,
    1px 1px 0 0 #201c29 inset;
  }
  
  .nav-left {
     display: flex;
     justify-content: center;
     align-items: center;
     padding: 1rem 2rem;
  }
  
  .icon-logo-star {
    fill: #ff8a00;
  }
  
  .icon-logo-text, .icon-search {
    fill:#fff;
  }
  
  .nav-mid {
    flex-grow: 4;
    height: 100%;
  }
  
  .nav-mid-items {
    display:flex;
    list-style-type: none;
    width: 100%;
    height: 100%;
  }
  
  .nav-mid-items li {
    flex-grow: 1;
    height: 100%;
  }
  
  
  .nav-mid-items a {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-style: normal;
    color: #fff;
    font-size: 0.7rem;
    font-weight: 700;
    padding: 1rem 0.5rem;
    box-shadow:
    1px 1px 0 0 #201c29,
    1px 1px 0 0 #201c29 inset;
  }
  
  .nav-mid-items a:hover {
    background: #312d3c;
  }
  
  .nav-right {
    display: flex;
    flex: 1 0 350px;
    justify-content: center;
    align-items: center;
  }
  
  .search-bar-container{
    display: flex;
    flex-grow: 1;
    border-radius: 28px;
    border: 2px solid #312d3c;
    margin: 0 1.5rem;
  }
  
  .search-bar-container input{
    font-family: Operator Mono A,Operator Mono B,Source Code Pro,Menlo,Consolas,Monaco,monospace;
    flex-grow: 1;
    font-size: 100%;
    background: #201c29;
    color: #fff;
    padding: .5rem 1rem;
    border: 0;
    border-top-left-radius: 27px;
    border-bottom-left-radius: 27px;
    outline: none;
  }
  
  .search-bar-container button{
    border: 0;
    border-top-right-radius: 27px;
    border-bottom-right-radius: 27px;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    padding: 0 22px 0 18px;
    background: linear-gradient(180deg,#ff8a00,#e52e71);
  }