.newsletter
            {
                font-weight: bold;
                font-size: 40px;
                font-family: Arial, Helvetica, sans-serif;
                color:white;
            }

            .newsletter-input
            {
                border-radius: 10px;
                padding:20px 45px;
                border:none;
                outline: none;
                font-size: 20px;
                font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
            }

            .newsletter-btn
            {
                border-radius: 10px;
                background-image:linear-gradient(to top left,#ff8a00,#e52e71)!important;
                padding:20px 35px;
                font-size: 20px;
                border:none;
                outline: none;
                margin-left: 10px;
                color:white;
                font-weight: bold;
                font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
            }

            .newsletter-btn:hover
            {
                background-image: linear-gradient(to top left,#ff8a00 30%,#e52e71)!important;
                transform: translateY(1px);
            }

            .sm-container 
            {
                display: flex;
                flex-wrap: nowrap;
                width:70%;
            }

            .sm-container > div 
            {
               
                width: 20%;
                margin: 10px;
                text-align: center;
                font-size: 30px;
                
            }
            .sm-name
            {
                
                font-family: helvetica;
                font-weight: bold;
                font-size: 22px;
                line-height: 0em;
                background: -webkit-linear-gradient(90deg,#ff8a00,#e52e71);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }

            .clr-multi
            {
                color:linear-gradient(90deg,#ff8a00,#e52e71);
            }
            .sm-desc
            {
                font-size: 22px;
                color: #7A7A8C;
                line-height: 0em;
            }

            .footer-container 
            {
                display: flex;
                flex-wrap: nowrap;
                justify-content: space-between;
                margin: 30px 80px;
            }

            .footer-container > div 
            {
              
                margin: 10px;
                justify-content: center;
                
            }

            .footer-left
            {
                font-size: 22px;
                font-family: helvetica;
                font-weight: 400px;
                line-height: 35px;
                color:#b1b1b1;
                width:58%;
                
            }

            .footer-right
            {
                float: right;
                
            }

            .footer-right td
            {
                padding:10px 10px;
                font-size: 24px;
                color:#b1b1b1;
                cursor: pointer;
            }

            .footer-right h2
            {
                font-size: 30px;
                font-family: helvetica;
                color: white;
                line-height: 0em;

            }

            .footer-card-holder
            {
                display: flex;
                width:95%;  
                margin: auto;
            }

            .footer-card-holder span
            {
                color:white;
                text-decoration:  underline;
                text-decoration-color:linear-gradient(90deg,#ff8a00,#e52e71) ;
            
            }

            .footer-card-holder span:hover
            {
                text-decoration-color: white ;
            }

            .clr-white
            {
                color:white;
                cursor: pointer;
            }

           

            .footer-card-holder > div 
            {
                width:33%;
                padding-top:2rem;
                padding-bottom: 1.7rem;
                padding-left: 2rem;
                padding-right:2rem;
                color:#a1a1af;
                background: #1f1c28;
                font-size: 16px;
                font-family: helvetica;
                text-align: center; 
                margin:10px;
                border-radius: 8px;
                border: 1px solid #312d3c;
            }


           



            