.card
{
   background-color:#17141D;
   min-height: 435px;
   min-width: 320px;
   padding:1.5rem;
   transition: 0.5s;
   border-radius: 16px;
   box-shadow: -1rem 0 3rem #000;
   margin-bottom: 45px;
}

.card:not(:first-child)
{
    margin-left:-6rem;
}

.card:hover
{
    transform: translateY(-1rem);
    ~ .card
    {
        background-color: red;
    }
}

.card:hover ~ .card
{
    transform: translateX(6rem);
}


.big-card
{
    height: 450px;
    width:330px;
    border-radius: 1rem;
    background-image: linear-gradient(45deg,#ff8a00,#e52e71);
   margin:20px 60px;
    position: relative;
}

.big-card p
{
    color:white;
    font-weight: bold;
    font-family: Helvetica;
    line-height: 1em;
    font-size: 30px;
    margin-right:50%;
}

            
.color-change:hover
{
    background: -webkit-linear-gradient(45deg,#ff8a00,#e52e71);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.card div 
{
    margin-top: 40%;
}


#mcards::-webkit-scrollbar
{
    margin-top:40px;
    height: 10px;
    width: 4px;
    background:linear-gradient(90deg,#201c29,#201c29 1px,#17141d 0,#17141d);;
   
        
}

#mcards::-webkit-scrollbar-thumb:horizontal
{
    background: #201c29;
    border-radius: 10px;
    box-shadow: inset 2px 2px 2px hsla(0,0%,100%,.25), inset -2px -2px 2px rgba(0,0,0,.25);
    
}


.article-cm
{
    font-size: 14px;
    color:white;
    font-weight: bold;
    font-family:Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
}

            
.author-info-panel-cm
{
    display: flex;
    width:90%;
    margin-bottom: 10px;
}

.author-info-panel-cm > div 
{
    margin: 1px
}


.author-img-cm
{
   
    width: 64px;
    height: 60px!important;
    display: block;
    overflow: hidden;
    margin: 10px 10px;
   
}

            
.author-comments-cm
{
    font-size: 13px;
    color:#7A7A8C;
    font-weight: bold;
    font-family: Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
}

.author-comments-designation-cm
{
    font-size: 13px;
    color: white;
    line-height: 0em;
    font-family: Lucida Grande,Lucida Sans Unicode,Tahoma,Sans-Serif;
}




            
.date-cm
{
    font-size: 14px;
    color:#7A7A8C;
    font-family: Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;;
}

.artical-heading-card
{
    font-size: 22px;
    color:white;
    font-weight: bold;
    line-height: 0.5em;
    cursor: pointer;
    font-family: Lucida Grande,Lucida Sans Unicode,Tahoma,Sans-Serif;
}


.half-circle-cm {
    position: absolute;
    bottom: 5;
    top:0px;
    left: 5px;
    fill: none;
    stroke: url(#orange-to-pink);
    stroke-width: 8;
    stroke-linecap: round;
    pointer-events: none;
}

.big-card a
{
    color:white;
    font-size: 18px;
    font-weight: bold;
}
