html::-webkit-scrollbar 
{
    width: 30px;
    height: 10px;
}

html::-webkit-scrollbar-thumb 
{
    background: linear-gradient(180deg,#ff8a00,#e52e71);
    border-radius: 30px;
    box-shadow: inset 2px 2px 2px hsla(0,0%,100%,.25), inset -2px -2px 2px rgba(0,0,0,.25);
}

html::-webkit-scrollbar-track 
{
    background: linear-gradient(90deg,#201c29,#201c29 1px,#100e17 0,#100e17);
}

.body
{
    font-family: Ringside Regular A,Ringside Regular B,
    Rubik,Lato,Lucida Grande,Lucida Sans Unicode,
    Tahoma,Sans-Serif;
    overflow: hidden;
}
.header-main-icon
{
    height: 50px;
    width:50px;
}
.navigator td
{
padding:20px 25px;
}

.website-name
{
font-size: 40px;
vertical-align: 10px;
font-weight: bold;
color: white;
font-family: sans-serif;
}

.navigator p
{
line-height: 0em;
font-weight: bold;
color:white;
font-family: sans-serif;
}

.navigator input, .c-table input
{
padding: 15px 60px;
color: gray;
border-top-left-radius: 25px;
border-bottom-left-radius: 25px;
outline:none;
border:none;
margin-right:-10px;
}

.navigator button, .c-table button
{
padding: 16.5px 28px;
border-top-right-radius: 30px;
border-bottom-right-radius: 30px;
border-top-left-radius: 10px;
border-bottom-left-radius: 10px;
outline:none;
border:none;
background-image:  linear-gradient(90deg,#ff8a00,#e52e71);
}

.navigator button i
{
font-size: 14px;
font-weight: bold;
color:white;
transform: rotate(90deg);
}



.navigator 
{
margin-top:0;
}

.navigator th,.navigator td, .navigator table
{
border-collapse: collapse;
border: 0.2px solid lightgray; 
}

/* author info*/

.article
{
    font-size: 20px;
    color:white;
    font-weight: bold;
    font-family:Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
}

.date
{
    font-size: 20px;
    color:#7A7A8C;
    font-family: Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;;
}

.latest-artical-heading
{
    font-size: 48px;
    color:white;
    font-weight: bold;
    line-height: 0em;
    cursor: pointer;
    font-family: Lucida Grande,Lucida Sans Unicode,Tahoma,Sans-Serif;
}

.artical-heading
{
    font-size: 38px;
    color:white;
    font-weight: bold;
    line-height: 1.5em;
    cursor: pointer;
    font-family: Lucida Grande,Lucida Sans Unicode,Tahoma,Sans-Serif;
    
}

.color-change:hover
{
background: -webkit-linear-gradient(45deg,#ff8a00,#e52e71);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}



.author-info-panel
{
    display: flex;
    width:47%;
    margin-top: 20px;
    margin-bottom: 20px;
}

.author-info-panel > div 
{
    margin: 1px;
    width:30%;
    align-items: center;
}


.author-img
{
    border-radius: 50%;
    width: 61px;
    height: 65px!important;
    display: block;
    overflow: hidden;
    margin: 10px 10px;
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
}

.author-img-circle
{
position: absolute;
bottom: 0;
left: 0;
width: 80px;
height: 56px;
fill: none;
stroke: -webkit-linear-gradient(45deg,#ff8a00,#e52e71);
stroke-width: 8;
stroke-linecap: round;
pointer-events: none;
}

.author-comments
{
    font-size: 15px;
    color:#7A7A8C;
    font-weight: bold;
    font-family: Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
}

.author-comments-designation
{
    font-size: 15px;
    color: white;
    line-height: 0em;
    font-family: Lucida Grande,Lucida Sans Unicode,Tahoma,Sans-Serif;
}


/*article content*/

.article-box
{
margin-left: 5%;
}

.article-content-box
{
    width:80%;
    font-size: 23px;
    color:white;
    line-height: 30px;
    font-weight: bold;
}

.button-rnd
{
    color:rgb(122,122,122);
    font-weight: bold;
    font-size: 10px;
    text-transform: uppercase;
    padding: 7px 15px;
    border: 3px solid rgb(40,36,47);
    border-radius: 20px;
    background-color: transparent;
    font-family: Lucida Grande,Lucida Sans Unicode,Tahoma,Sans-Serif;
    margin-left: 5px;
}

.button-rnd:hover
{
border: 3px solid white;
}


/*advertisement*/
.adv-box
{
padding:20px 20px;
width:70%;
margin: 10% 10%;
}

.adv-title
{
text-transform: uppercase;
font-size: 12px;
letter-spacing: 2px;
color:rgb(3,169,244);
line-height: 0em;
}

.adv-box img
{
height: 250px;
width: 300px;
border-radius: 12px;
}

.adv-content-box
{
width:90%;
margin:5px 10%;

}

.adv-title:hover
{
color:white;
}

.adv-description
{
font-size: 13px;
color:#A1A1AF;
}


.read-article
{
    font-size: 23px;
    background: -webkit-linear-gradient(90deg,#ff8a00,#e52e71);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-decoration: #ff8a00 underline;
    font-weight: 700;
    padding-bottom: .15rem;
    cursor: pointer;
    font-family: Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
}

.read-article:hover
{
    text-decoration: white underline;
}

