.wrapper
{
    display:grid;
    width:100%;
    grid-template-columns: 30% 68%;
    grid-gap: 16px;
    
}



.btn-pad
{
   margin-left:40px ;
}



/*two cards*/
.card-small-box
{
background-color: chocolate;
width:40%;
padding:20px 20px;

}

.card-small-box img
{
display: inline-block;

}

.card-small-content-box
{
width:60%;
display: inline-block;
}

.card-small-title
{
font-size: 25px;
font-weight: bold;
line-height: 0em;
}

.card-small-content
{
font:14px;

}



.grid-container {
display: grid;
grid-template-columns: 70% 30%;
}


.browse-btn
{
    width:85%;
    height:70px;
    background-image:linear-gradient(to top left,#ff8a00,#e52e71)!important;
    border: none;
    outline: none;
    border-radius: 10px;
    color: white;
    font-size: 24px;
    font-weight: bold;
    font-family: Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
    margin-left: 5%;
}

.browse-btn:hover
{
    background-image: linear-gradient(to top left,#ff8a00 30%,#e52e71)!important;
    transform: translateY(1px);
}


.clr-line
{
    width:85%;
    background-image: linear-gradient(90deg,#ff8a00,#e52e71);
    height: 4px;
    border-radius: 2px;
    border:none;
    outline: none;
}


.half-circle {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 80px;
    height: 56px;
    fill: none;
    stroke: url(#orange-to-pink);
    stroke-width: 8;
    stroke-linecap: round;
    pointer-events: none;
}

.cards
{
    display: flex;
    margin: 40px 20px;
    overflow-x: scroll;
    background-color: #100e17;
}

.multiple-article-container 
{
    display: grid;
    grid-template-columns: 30% 70%;
}

.ma-item 
{
    padding: 1px;
}

.big-card-cntn
{
    margin-left:'40px';
    bottom:'30px';
    position:'absolute';
}

.big-card-spn
{
    line-height:'2px';
    color:'#ffb4b4';
    font-size:'18px';
}


.adv-big
{
    background-color:1f1f1f;
    width:70%;
    font-family: helvetica;
    position:sticky;
    margin-left:70px;
    top:30px;
}

.adv-big-top
{
    background:linear-gradient(90deg,#c02d28,#e66225);
    padding: 0.2rem 1rem;
   
    color:white;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.adv-big-top p
{
    text-transform: uppercase;
    padding-top: 5px;
    font-size: 13px;
    font-weight: bold;
    letter-spacing: 2px;
}

.adv-big-bottom
{
    background-color: #1f1f1f;
    padding:1rem 1rem;
    color:white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  padding-left: 25px;
  padding-right: 25px;
}

.adv-big-bottom p
{
    font-size: 14px;
    color:#d8d8d8;
}

.adv-big-bottom h3
{
    line-height: 1em;
    font-size: 20px;
}

