

            .card-small-title
            {
                font-size: 25px;
                font-weight: bold;
                line-height: 0em;
            }

            .card-small-content
            {
                font:14px;
               
            }

            .small-card-container 
            {
                display: flex;
                background-color: #201c29;
                width:45%;
                height:180px;
                color:white;
                border-radius: 5px;
            }

            .small-card-container > div 
            {
                padding:15px 15px;
                margin: 5px;
                width:30%;
                font-size: 14px;
            }
            .small-card-container img
            {
                padding-top:5px 10px;
                padding-right: 8px;
            }

            .small-card-container-cntnt
            {
                color:#A1A1AF;
            }


            .two-card-container 
            {
                display: flex;
                margin-left: 7px;
            }

            .two-card-container > div
            {
                margin: 7px;
            }